              console.log('match',data.txtforeign)

             this.foreignadd(data.txtforeignfull,data.txtforeignuser,data.txtforeign);
              
           // }
//else{
            //  this.presentToast('check your input');
      
//console.log('nomatch')
      
          //  }
           // if (a==1)) {
              // logged in!
           // } else {
              // invalid login
             // return false;
            //}
